import React , {useState} from 'react';
import { useHistory, useParams } from 'react-router-dom';
import { Table,DropdownButton,Dropdown, ButtonGroup, Nav, NavDropdown, Form, Button, FormControl  }  from 'react-bootstrap';
import { connect} from 'react-redux';

function Cart (props) {
   

    // 모달로 만들어서 장바구니에 물건이 있으면 보여주고 없으면 보여주지 않음
    return(
        <div>
            <Table responsive="sm">
                <thead>
                <tr>
                    <th>#</th>
                    <th>상품명</th>
                    <th>수량</th>
                    <th>변경</th>
               
                </tr>
                </thead>
                <tbody>
                {
                    props.state.map((a,i)=>{
                        return(
                        <tr key={i} >
                            <td>{ a.id }</td>
                            <td>{ a.name }</td>
                            <td>{ a.quan}</td>
                            <td>
                                
                                <button onClick = { () => { props.dispatch( {type : '수량증가' }) }}></button> 
                                <button onclick = { () => { props.dispatch( {type : '수량감소' }) }}>-</button> 
                            </td>                       
                          </tr>
                        )
                        
                    }
                    )
                    
                }<tr>
                    
                 
                </tr>
                </tbody>
                
                
                
                <div className="my-alertw">
                <p> 지금 구매하시면 신규 할인 20% </p>
                </div>
            </Table>
        </div>
    )


}

function state를props (state) {
 return {
     state : state
 }
} 

export default connect(state를props) (Cart) 
